/*cid=0,page=1,2,3,4,包含了所有数据*/
/*https://api.shop.eduwork.cn/api/goods?category_id=0&page=5&sales=1*/
export function getCategoryGoods(order='sales',cid=0,page=1) {
    return request({
        url:"/api/goods?category_id="+cid+'&page='+page+'&'+order+'=1',
        /*method:'get',
        params:{

        }*/
    })
}


