import {createRouter, createWebHistory} from 'vue-router';
import store from '../store';                             // 引入store模块
import {ElMessage} from 'element-plus';


const Home = () => import('../views/home/Home.vue');
const Category = () => import('../views/category/Category.vue');
const Search = () => import('../views/search/Search.vue');
const Detail = () => import('../views/detail/Detail.vue');
const ShopCart = () => import('../views/shopcart/ShopCart.vue');
const Profile = () => import('../views/profile/Profile.vue');
const Register = () => import('../views/profile/Register.vue');
const Login = () => import('../views/profile/Login.vue');
const CreateOrder = () => import('../views/order/CreateOrder.vue');
const Order = () => import('../views/order/Order.vue');
const OrderDetail = () => import('../views/order/OrderDetail.vue');
const ErrorPage = () => import('../views/404error.vue');
const Address = () => import('../views/address/Address.vue');
const AddressEdit = () => import('../views/address/AddressEdit.vue');
const Collection = () => import('../views/profile/Collection.vue');
const Settings = () => import('../views/profile/Settings.vue');

const routes = [
    {
        path: '/',
        name: 'DefaultHome',  // 默认的路由
        component: Home,
        meta: {
            title: 'GoBook——主页',
            keepSearchAlive: true
        }
    },
    {
        path: '/home',
        name: 'Home',
        component: Home,
        meta: {
            title: 'GoBook——主页',
            keepSearchAlive: true
        }
    },
    {
        path: '/category',
        name: 'Category',
        component: Category,
        meta: {
            title: 'GoBook——书籍分类',
            keepSearchAlive: true,
            alias: '书籍分类',
        }
    },
    {
        path: '/search',
        name: 'Search',
        component: Search,
        meta: {
            title: 'GoBook——查找书籍',
            keepSearchAlive: true,
            alias: '查找书籍',
        },

        beforeEnter: (to, from) => {             // 路由独享的守卫的from不会来自本身，只能是undefined
            if (Object.keys(to.query).length > 0 && from.path !== '/detail') {         // 把数据放在浏览器中
                if (to.query.SearchText.length > 0) {
                    var new_text = to.query.SearchText;
                    if(new_text.startsWith('%') === true){
                        new_text = decodeURIComponent(new_text);
                    }
                    window.localStorage.setItem("lastSearchWords", new_text);
                }
            } else if (from.path !== '/detail') {
                window.localStorage.setItem("lastSearchWords", "");
            }

        }
    },
    {
        path: '/detail',
        name: 'Detail',
        component: Detail,
        meta: {
            title: 'GoBook——商品详情'
        },
        beforeEnter: (to, from) => {             // 路由独享的守卫的from不会来自本身，只能是undefined
            if (from.meta.alias !== undefined && from.fullPath !== '/') {         // 把数据放在浏览器中
                window.localStorage.setItem("lastPage", from.meta.alias);
                window.localStorage.setItem("lastPagePath", from.path);
                if (Object.keys(from.query).length > 0) {
                    window.localStorage.setItem("lastPageQuery", from.fullPath);
                } else {
                    window.localStorage.setItem("lastPageQuery", '');
                }
            } else if (from.fullPath === '/' || from.fullPath === '/home') {
                window.localStorage.setItem("lastPage", '');
                window.localStorage.setItem("lastPagePath", '');
                window.localStorage.setItem("lastPageQuery", '');
            }
        },
    },
    {
        path: '/shopcart',
        name: 'ShopCart',
        component: ShopCart,
        meta: {
            title: 'GoBook——购物车',
            isAuthRequired: true,           // 需要登录
            alias: '购物车',
        }
    },
    {
        path: '/profile',
        name: 'Profile',
        component: Profile,
        meta: {
            title: 'GoBook——个人中心',
            isAuthRequired: true,
            alias: '个人中心',
        }
    },
    {
        path: '/collection',
        name: 'Collection',
        component: Collection,
        meta: {
            title: 'GoBook——商品收藏',
            isAuthRequired: true,
            alias: '商品收藏',
        }
    },
    {
        path: '/register',
        name: 'Register',
        component: Register,
        meta: {
            title: 'GoBook——用户注册',
            isMaster: true
        }
    },
    {
        path: '/login',
        name: 'Login',
        component: Login,
        meta: {
            title: 'GoBook——用户登录',
            isMaster: true
        }
    },
    {
        path: '/settings',
        name: 'Settings',
        component: Settings,
        meta: {
            title: 'GoBook——功能设置',
            isMaster: true
        }
    },
    {
        path: '/address',
        name: 'Address',
        component: Address,
        meta: {
            title: 'GoBook——地址管理',
            isAuthRequired: true,
            alias: '地址管理',
        }
    },
    {
        path: '/addressedit',
        name: 'AddressEdit',
        component: AddressEdit,
        meta: {
            title: 'GoBook——地址编辑',
            isAuthRequired: true,
            alias: '地址编辑',
        }
    },
    {
        path: '/createorder',
        name: 'CreateOrder',
        component: CreateOrder,
        meta: {
            title: 'GoBook——创建定单',
            isAuthRequired: true,           // 需要登录
            alias: '创建定单',
        }
    },
    {
        path: '/order',
        name: 'Order',
        component: Order,
        meta: {
            title: 'GoBook——定单管理',
            isAuthRequired: true,           // 需要登录
            alias: '定单管理',
        }
    },
    {
        path: '/orderdetail',
        name: 'OrderDetail',
        component: OrderDetail,
        meta: {
            title: 'GoBook——订单详情',
            isAuthRequired: true,           // 需要登录
            alias: '订单详情',
        }
    },
    {
        path: '/404error',
        name: 'ErrorPage',
        component: ErrorPage,
        meta: {
            title: 'GoBook——出错啦！页面不存在'
        }
    },
];

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
});

router.beforeEach((to, from, next) => {
    if (to.meta.title === undefined) {          // 自定义 重定向页面
        return next('/404error');
    }
    // getters属性中的方法，可以实时监听数据的改变

    if (to.meta.isAuthRequired === true
        && (store.getters.getLoginState === false || store.getters.getLoginState === undefined)
        && to.meta.isMaster === undefined) {

        ElMessage.error('您还没有登录，请先登录');
        return next('/login');
    } else {
        next();
    }
    document.title = to.meta.title;
});

export default router
