import {request} from './request';

// 添加/取消收藏
export function changeCollection(id){
    return request({
        url:`/api/collects/goods/${id}`,
        method:'post',
    })
}
// 获取收藏列表
export function getCollectList(page){
    return request({
        url:'/api/collects?page='+{page},
        method:'get',
    })
}