
const getters = {
    // 获取登录状态
    getLoginState(state){
        if(window.localStorage.getItem('token')!==null &&  window.localStorage.getItem('token').length > 0){
            return true;
        }
        return state.user.islogin;
    },
    // 获取购物车的数量
    getShopCount(state){
        return state.cartCount;
    },

    // 获取默认地址的Id
    getAddressId(state){
        return state.addressId;
    },
    // 获取默认地址的
    getAddressUserName(state){
       return state.addressUserName;
    },
    // 获取默认地址的Phone
    getAddressUserPhone(state){
       return state.addressUserPhone;
    },
    // 获取默认地址的Province
    getAddressUserProvince(state){
       return state.addressUserProvince;
    },
    // 获取默认地址的City
    getAddressUserCity(state){
       return state.addressUserCity;
    },
    // 获取默认地址的County
    getAddressUserCounty(state){
       return state.addressUserCounty;
    },
    // 获取默认地址的Detail
    getAddressUserDetail(state){
       return state.addressUserDetail;
    },
    // 获取当前帐号的用户头像
    getUserFace(state){
       return state.userFace;
    },
    // 获取当前帐号的用户昵称
    getUserNickname(state){
       return state.userNickname;
    },
    // 获取当前帐号的用户email
    getUserEmail(state){
       return state.userEmail;
    },
};
export default getters;