import {createStore} from 'vuex'
import mutations from './mutations';
import actions from './actions';
import getters from './getters';

const state = {
    user: {
        isLogin:window.localStorage.getItem('token') ? true : false
    },
    cartCount: 0,
    addressId:0,
    addressUserName:'',
    addressUserPhone:'',
    addressUserProvince:'',
    addressUserCity:'',
    addressUserCounty:'',
    addressUserDetail:'',
    userFace:'',
    userNickname:'',
    userEmail:''
};
// addressId是否为0,成为了是否有默认值的决定性属性
// userNickname是否为"",成为了是否有默认值的决定性属性

export default createStore({
    state,
    mutations,
    actions,
    getters,
    modules: {}
})
