<template>
    <!--头部栏内容-->
    <div class="header">
        <div class="container-fluid px-2 px-sm-3 px-md-5 py-3">
            <div class="row d-flex justify-content-between align-items-center pt-2">
                <!--icon图标栏-->
                <div class="header-logo col-4 col-md-3  " style="text-align:left;">
                    <img src="~assets/images/gobook-logo.png" @click="$router.push({path:'/'})" class="_logo-img"
                         style="max-width:160px;" alt="">
                </div>
                <!--搜索栏： 大屏显示-->
                <div class="header-search d-none d-md-block col-md-5 " v-if="$route.meta.keepSearchAlive">
                    <div class="input-group my-2">
                        <input type="text" name="searchMessage" class="searchMessage form-control"
                               placeholder="搜一搜商品类型" v-model="searchT"  >
                        <div class="input-group-append">
                            <span class="input-group-text btn btn-danger  " @click="searchBook">搜索</span>
                        </div>
                    </div>
                </div>
                <!--用户栏-->
                <div class="_login header-author col-6 col-md-4 d-flex justify-content-end">
                    <div class=" d-flex justify-content-between align-items-center flex-column flex-sm-row
                         px-4 px-sm-3 px-md-4" style="color:red;" @click="goToCart">
                        <div v-if="$store.getters.getShopCount!==0">
                            <el-badge :value="$store.getters.getShopCount" :max="9" type="danger" class="item">
                                <i class="iconfont icon-shop-cart  d-inline "
                                   style="font-size:24px;width:24px;height:auto;color:red;"></i>
                            </el-badge>
                        </div>
                        <div v-else>
                            <i class="iconfont icon-shop-cart  d-inline " style="font-size:24px;width:24px;height:auto;color:red;"></i>
                        </div>
                        <span class="d-none txt-body  font-weight-bold px-1  d-sm-inline-block text-truncate">  购物车 </span>
                        <span class="d-block  txt-mini font-weight-bold ml-5  d-sm-none text-truncate">购物车</span>
                    </div>
                    <div class="d-flex justify-content-between align-items-center  flex-column flex-sm-row
                         px-2 " style="color:#909399" @click="goToLogin">
                        <img :src="$store.getters.getUserFace" @click="$router.push({path:'/'})"
                             style="max-width:40px;" alt="" v-if="$store.getters.getUserFace.length!==0">
                        <i class="iconfont icon-yonghu2 d-inline" v-else
                           style="font-size:24px;width:25px;height:auto;color:#ff1111;"></i>

                        <div class="d-none txt-body font-weight-bold px-1  d-sm-inline-block text-truncate">
                            <span v-if="$store.getters.getUserNickname.length !== 0">{{$store.getters.getUserNickname}}</span>
                            <span v-else>登录/注册</span>
                        </div>
                        <div class="d-block txt-mini font-weight-bold ml-5  d-sm-none text-truncate">
                            <span v-if="$store.getters.getUserNickname.length !== 0">{{$store.getters.getUserNickname}}</span>
                            <span v-else>登录/注册</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!--搜索栏：中小屏显示-->
        <div class=" d-block d-md-none col-md-12 px-2  px-sm-3  mb-1" v-if="$route.meta.keepSearchAlive">
            <div class="input-group     rounded rounded-lg">
                <input type="text" name="searchMessage" class="searchMessage form-control "
                       placeholder="搜一搜商品类型" v-model="searchT" >
                <div class="input-group-append">
                    <span class="input-group-text btn btn-danger  searchButton" @click="searchBook">搜索</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import $ from 'jquery';            // 引入$符。
    import {getCategory, getCategoryGoods} from 'network/category';
    import {getAddressList} from 'network/address';
    import {getUser} from 'network/user';

    import {ElLoading, ElMessage} from 'element-plus';
    import {onMounted, ref, reactive} from 'vue';
    import {useRouter} from 'vue-router';
    import {useStore} from 'vuex';

    export default {
        name: "HeaderBar",
        setup() {
            const router = useRouter();
            const store = useStore();

            // 输入框的数据
            let isErrorInput = ref(false);
            let isEmptyInput = ref(false);
            let searchT = ref('');

            onMounted(() => { // 分类数据列表项
                initShopCount();
                initAddress();
                initProfile();
            });


            // 初始化数据，设置vuex中的内容
            const initShopCount = () => {
                if (store.getters.getShopCount.length === 0) {   // 商品数量来决定是否重新获取商品数据
                    store.dispatch('updateCart');                   // 分发数据，更新cartCount的值
                } else {
                    console.log('当前已经有商品数量',store.getters.getShopCount)
                }
            };

            // 初始化数据，设置vuex中的内容
            const initAddress = () => {
                if (store.getters.getAddressId === 0) {
                    getAddressList().then(res => {
                        console.log("initAddress-data",res);
                        if (res.data.length === 0) {
                            return;
                        }
                        res.data.forEach(item => {           // 参数必需符合地址列表格式
                            if (item.is_default) {
                                store.commit('setAddressId', item.id);      // 给vuex提交地址的ID
                                store.commit('setAddressUserName',item.name);
                                store.commit('setAddressUserPhone',item.phone);
                                store.commit('setAddressUserProvince',item.province);
                                store.commit('setAddressUserCity',item.city);
                                store.commit('setAddressUserCounty',item.county);
                                store.commit('setAddressUserDetail',item.address);
                            }
                        });
                    }).catch(err => {
                        console.log('initAdodress-error', err);
                        // ElMessage.error('获取地址数据失败！');
                    });

                } else {
                    console.log('当前存在默认地址数据', store.getters.getAddressId);
                }
            };

            // 初始化数据，设置vuex中的内容
            const initProfile = () => {
                if (store.getters.getUserNickname.length === 0) {   // 用户的昵称来决定是否重新获取用户数据

                    getUser().then(res => {
                        console.log("initProfile-data", res);      // 这里，是因为后端没有设置返回的状态码
                        if (res.id) {
                            store.commit('setUserNickname', res.name);
                            store.commit('setUserFace', res.avatar_url);
                            store.commit('setUserEmail', res.email);
                        }
                    }).catch(err => {
                        console.log('initProfile-err', err);
                        //ElMessage.error('获取用户信息失败，靖刷新页面');
                    });
                } else {
                    console.log('当前已经有默认用户信息',store.getters.getUserNickname)
                }
            };

            // 查询书箱
            const searchBook = () => {                    //  v-model 将两个输入框同步在一起
                var pattern = /!|@|#|\$|%|^|&|\*|\(|\)|{|\[|\|\]|:|;|'|"|<|>|\?|\s/g;
                var searchText = $('.searchMessage').val();
                if (searchText.length === 0) {
                    isEmptyInput.value = true;
                    ElMessage.error('搜索栏的数据是空的！');
                } else if (searchText.search(pattern) !== -1) {
                    var tText = searchText.replace(pattern, '');
                    if (tText.length !== 0) {
                        $('.searchMessage').val(tText);              // 修正搜索值
                    } else {
                        isErrorInput.value = true;
                        ElMessage.warning('搜索栏的数据不能全是非法值！');
                    }
                }
                if (isErrorInput.value === false && isEmptyInput.value === false) {
                    itemClick(searchText);                 // 跳转页面
                    searchT.value = '';
                }
                isErrorInput.value = false;
                isEmptyInput.value = false;
            };

            // 当用户点击后键盘后，判断是否输入信息
                /*只有在输入框获取输入焦点后，并且输入框内有数据，再回车确认才会进行搜索*/
            $(document).keydown(function(e){
                e = e || window.event;
                if (e.keyCode === 13 && $('.searchMessage').val().length !== 0) {
                    searchBook();                        // 确认搜索信息
                }
            });

            // 搜索的数据合法，跳转搜索页
            const itemClick = (SearchText) => {
                router.push({path: '/search', query: {SearchText}});
            };

            // 点击购物车，跳转购物车页
            const goToCart = () => {
                router.push({path: '/shopcart'});
            };
            // 点击登录，跳转登录页
            const goToLogin = () => {
                router.push({path: '/profile'});
            };


            return {searchBook, searchT, goToCart, goToLogin};
        }
    }
</script>

<style scoped lang="scss">
    .header {
        height: auto;
        width: 100%;
        overflow: hidden;
        position: relative;
        top: 0;
        left: 0;
        right: 0;

        .logo-img {
            /*width:250px;*/
            height: auto;
        }

        min-width: 340px;
    }

    input::placeholder {
        color: #dedcdf;
    }

</style>