<template>
    <!--主页内容-->
    <div v-if="!appearError">
        <div class="wrapper">
            <div class="content">
                <!--轮播图商品栏-->
                <home-swiper :banners="banners" class="mySwiperLoading"></home-swiper>

                <!--推荐商品栏-->
                <div class="recommend text-white py-1 d-flex flex-column justify-content-start "
                     style="font-size:18px;font-weight:bold;">
                    <span class="txt-body  bg-danger p-2 rounded rounded-lg" style="width:160px;">推荐书籍</span>
                    <hr class="mt-0" style="height:2px;color:red">
                </div>
                <recommend-view :recommends="recommends"></recommend-view>
                <!--推荐商品栏，数据不存在，提示信息-->
                <div v-if="!init_recommends" class="recommend-box">
                    <el-empty description="推荐商品暂无数据,刷新试一试"></el-empty>
                </div>

                <!--畅销商品栏-->
                <div class="recommend text-white py-1 d-flex flex-column justify-content-start "
                     style="font-size:18px;font-weight:bold;">
                    <span class="txt-body bg-danger p-2 rounded rounded-lg" style="width:160px;">畅销书籍</span>
                    <hr class="mt-0" style="height:2px;color:red">
                </div>
                <goods-list :goods="showGoods"></goods-list>
                <!--畅销商品栏，数据不存在，提示信息-->
                <div v-if="!init_goods">            <!--商品暂无数据-->
                    <el-empty description="商品暂无数据,刷新试一试"></el-empty>
                </div>

                <!--底部信息栏-->
                <div class="w-100 text-center mb-3" v-if="init_goods">
                    <span v-if="!isTotal" @click="myRefresh" class="add_more">点击加载更多</span>
                    <span v-else @click="myRefresh" class="add_more">已经加载完毕啦！</span>
                </div>
            </div>
        </div>
        <back-top @bTop="bTop" class="myloading" v-show="isShowBackTop" v-if="init_goods"></back-top>
    </div>
    <div v-else>
        <img src="~assets/images/404error.png" alt="">
    </div>
</template>

<script>
    import $ from 'jquery';
    import HomeSwiper from './childcomps/HomeSwiper.vue';
    import RecommendView from './childcomps/RecommendView.vue';
    import GoodsList from "components/content/goods/GoodsList.vue";
    import BackTop from "components/common/backtop/BackTop.vue";

    import {ElLoading, ElMessage} from 'element-plus';
    import {getHomeAllData, getHomeGoods} from 'network/home';
    import {onMounted, ref, reactive, computed, watch, toRefs} from 'vue';

    export default {
        name: "Home",
        components: {
            HomeSwiper,
            RecommendView,
            GoodsList,
            BackTop
        },
        setup() {
            // 初始化检测数据
            const init_ok = reactive({
                init_banners: false,
                init_recommends: false,
                init_goods: false
            });
            // 推荐书箱的数据
            const appearError = ref(false);   // 检测页面是否为404
            const recommends = ref([]);       // 推荐数据
            const banners = ref([]);          // 轮播图数据

            // 商品列表数据模型，保存所有数据
            const goods = reactive({
                sales: {page: 1, list: []},
                recommend: {page: 1, list: []},
                new: {page: 1, list: []},
            });

            // 记录当前的页面类型,默认sales
            let currentType = ref('sales');

            // 检测滑动是否到达底部
            let isShowBackTop = ref(false);
            let isArriveBottom = ref(false);
            let isTotal = ref(false);
            let isTotalMesaage = ref(false);

            onMounted(() => {
                // banners和轮播图的图片
                let loadingInstance_init = ElLoading.service({
                    fullscreen: false,
                    text: '加载页面中。。。',
                    target: '.mySwiperLoading'
                });
                getHomeAllData().then(res => {
                    banners.value = res.slides;                // 轮播图数据
                    recommends.value = res.goods.data;         // 推荐数据


                    if (banners.value.length !== 0) {          // 检测轮播图数据是否为空
                        init_ok.init_banners = true;
                    } else {
                        /*appearError.value = true;*/
                    }
                    if (recommends.value.length !== 0) {       // 检测推荐商品数据是否为空
                        init_ok.init_recommends = true;
                    }
                    console.log('home-data',res);

                }).catch(err => {
                    console.log('home-err',err);
                    ElMessage.error('请求数据失败！');
                });
                if (loadingInstance_init !== null) {
                    loadingInstance_init.close();                // 页面加载中...
                    loadingInstance_init = null;
                }

                // 商品详情页,默认只有sales数据
                getHomeGoods('sales').then(res => {
                    goods.sales.list = res.goods.data;
                    init_ok.init_goods = true;
                });
                // getHomeGoods('new').then(res=>{
                //     goods.new.list = res.goods.data;
                // });
                // getHomeGoods('recommend').then(res=>{
                //     goods.recommend.list = res.goods.data;
                // });

            });

            // 监听窗口滚动事件
            $(window).scroll(function () {
                if (Math.ceil($(document).scrollTop()) >= $(document).height() - $(window).height()) {
                    isArriveBottom.value = isShowBackTop.value = true;
                    getMoreMessage();              // 调用函数
                } else {
                    isArriveBottom.value = false;
                    if ($(document).scrollTop() < $(window).height()) {
                        isShowBackTop.value = false;
                    } else {
                        isShowBackTop.value = true;              // 在滑动超过一定高度的时候，必需显示
                    }
                }
            });

            // 下拉或点击”加载更多“添加更多数据
            const getMoreMessage = () => {

                const page = goods[currentType.value].page + 1;
                if (isTotalMesaage.value === false) {
                    let loadingInstance = null;
                    if (isArriveBottom.value === true)
                        loadingInstance = ElLoading.service({fullscreen: false, text: '加载', target: '.myloading'});
                    getHomeGoods(currentType, page).then(res => {
                        goods[currentType.value].list.push(...res.goods.data);

                        if (res.goods.data.length === 0) {                            // 数据加载完毕
                            isTotalMesaage.value = true;
                            $('.add_more').html('已经加载完毕啦！');
                            $(".add_more").off("click");                     // 解绑点击事件,没效果
                            isTotal.value = true;
                        }
                    }).catch(err => {         // 默认请求数据异常时，中断数据请求
                        isTotalMesaage.value = true;            // 数据已经全部加载完毕
                        $('.add_more').html('已经加载完毕啦！');
                        $(".add_more").off("click");                     // 解绑点击事件,没效果
                        isTotal.value = true;
                    });
                    if (loadingInstance !== null) {
                        loadingInstance.close();
                        loadingInstance = null;
                    }
                    goods[currentType.value].page += 1;                        // 如果请求的页面数据出现异常，这里采用跳过的方式
                } else {
                    $('.add_more').html('已经加载完毕啦！');
                    $(".add_more").off("click");                     // 解绑点击事件,没效果
                    isTotal.value = true;
                }
            };

            // 显示数据
            const showGoods = computed(() => {
                if (goods[currentType.value].list.length !== 0) {       // 检测商品数据是否为空
                    init_ok.init_goods = true;
                } else {
                    init_ok.init_goods = false;
                }
                return goods[currentType.value].list;
            });

            // 退回顶部
            const bTop = () => {
                // 500毫秒内，滚动到顶部
                $('body,html').animate({scrollTop: 0}, 200);//回到顶端
                isShowBackTop.value = isArriveBottom.value = false;
            };

            // 点击”加载更多“
            const myRefresh = () => {
                if (!isTotal.value) {
                    isArriveBottom.value = isShowBackTop.value = true;
                    getMoreMessage();
                }
            };

            return {
                banners, appearError, recommends, goods, showGoods, bTop,
                isShowBackTop, ...toRefs(init_ok), myRefresh, isTotal
            }
        }
    }
</script>

<style lang="scss">

    .recommend-box {
        position: relative;

        .el-empty {
            max-height: 120px;

            .el-empty__image svg {
                margin-top: 0;
                position: relative;
                max-height: 100px;
            }
        }
    }

    .add_more:hover {
        color: red;
    }
</style>