<template>
    <!--下拉滚动内容-->
    <div class="backTop" @click="backtop"> </div>
</template>

<script>
    export default {
        name: "BackTop",
        setup(props,{emit}){
            return {
                backtop:()=>{
                    emit('bTop');
                }
            }
        }
    }
</script>

<style scoped lang="scss">
    .backTop{
        width: 40px;
        height: 40px;
        position: fixed;
        bottom: 80px;
        right: 30px;
        z-index:200;
        box-shadow:5px 5px 5px #888888;
        border-radius: 50%;
        background:#fcfcfc url('~assets/images/upward.png') no-repeat center center;
    }
</style>