<template>
    <!--轮播图内容-->
    <div id="swiper" class="container-fluid">
        <div v-if="banners.length===0">
            <!--轮播图内容栏，空内容显示-->
            <el-carousel :interval="4000" arrow="always" height="200px">
                <el-carousel-item v-for="item in 6" :key="item">
                    <h3 class="medium font-weight-bold" style="font-size:18px; margin-top:-50px;">没数据啦！刷新页面一下哦！</h3>
                </el-carousel-item>
            </el-carousel>
        </div>
        <!--轮播图内容栏，大屏显示-->
        <div class="d-none d-lg-block  " v-if="banners.length>0">
            <el-carousel :interval="4000" type="card">
                <el-carousel-item v-for="(item,index) in banners.slice(0,6)" :keys="index">
                    <h3 class="medium" @click.prevent="goD(item.id)">
                        <el-image :src="item.img_url"></el-image>
                    </h3>
                </el-carousel-item>
            </el-carousel>
        </div>
        <!--轮播图内容栏，中小屏显示-->
        <div class="d-block d-lg-none   " v-if="banners.length>0">
            <el-carousel :interval="4000" arrow="always">
                <el-carousel-item v-for="(item,index) in banners.slice(0,6)" :keys="index">
                    <h3 class="medium" @click.prevent="goD(item.id)">
                        <el-image :src="item.img_url"></el-image>
                    </h3>
                </el-carousel-item>
            </el-carousel>
        </div>
    </div>
</template>
<script>
    import $ from 'jquery';            // 引入$符。
    import {useRouter} from "vue-router";

    export default {
        name: "HomeSwiper",
        props: {
            banners: Array,
            default() {
                return [];
            }
        },
        setup() {
            const router = useRouter();
            const goD = (id) => {
                router.push({path: '/detail', query: {id}});
            };
            return {goD}
        }
    }
</script>

<style>
    .el-carousel__container {
        height: 300px;
    }

    .el-carousel__indicators {
        max-height: 60px;
    }

    .el-carousel__item h3 {
        color: #475669;
        font-size: 14px;
        opacity: 0.75;
        height: 300px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0;
    }

    .el-carousel__item:nth-child(2n) {
        background-color: #99a9bf;
    }

    .el-carousel__item:nth-child(2n + 1) {
        background-color: #d3dce6;
    }

    .swiper {
        height: 300px;

    }

    .el-image__inner {
        vertical-align: middle;
    }

    img {
        width: 100%;
        height: auto !important;
    }

</style>