import {request} from './request.js';

// 注册
export function getRegister(data){
    return request({
        url:"/api/auth/register",
        method:'post',
        data
    })
}
// 登录
export function getLogin(data){
    return request({
        url:"/api/auth/login",
        method:'post',
        data
    })
}
// 退出登录
export function getLogout(){
    return request({
        url:"/api/auth/logout",
        method:'post',
    })
}

// 获取用户信息
export function getUser(){
    return request({
        url:"/api/user",
        method:'get'
    })
}

// ---- 暂无法更新数据-----
// 修改用户昵称 :  只允许修改用户昵称  name="'
export function setUserName(params){
    return request({
        url:"/api/user",
        method:'put',
        params
    })
}

// 修改用户头像  avatar="'
export function setUserAvatar(params){
    return request({
        url:"/api/user/avatar",
        method:'patch',
        params
    })
}

