<template>
    <div id="my_app">
        <!--头部栏-->
        <div id="header">
            <HeaderBar></HeaderBar>
        </div>
        <!--导航栏-->
        <div id="nav" class="px-md-5  w-100" style="background:#ffab00;z-index:99;">
            <ul class="d-flex list-unstyled justify-content-around align-items-center py-2 " style="width:100%;">
                <li>
                    <router-link class="tab-bar-item" to="/">
                        <div>首页</div>
                    </router-link>
                </li>
                <li>
                    <router-link class="tab-bar-item" to="/category">
                        <div class="d-inline-block">分类</div>
                        <div class="d-none d-md-inline-block">页面</div>
                    </router-link>
                </li>
                <li>
                    <router-link class="tab-bar-item" to="/order">
                        <div class="d-inline-block">订单</div>
                        <div class="d-none d-md-inline-block">管理</div>
                    </router-link>
                </li>
                <li>
                    <router-link class="tab-bar-item" to="/address">
                        <div class="d-inline-block">地址</div>
                        <div class="d-none d-md-inline-block">管理</div>
                    </router-link>
                </li>
            </ul>

        </div>
        <!--内容栏-->
        <div id="content">
            <router-view/>
        </div>
        <!--底部栏-->
        <div id="footer">
            <FooterBar></FooterBar>
        </div>
    </div>

</template>

<script>
    import {onMounted} from 'vue';
    import {useRouter} from 'vue-router';
    import $ from 'jquery';            // 引入$符。

    import HeaderBar from 'components/common/headerbar/HeaderBar';
    import FooterBar from 'components/common/footerbar/FooterBar';
    import HomePage from 'views/home/Home';

    export default {
        name: 'App',
        components: {
            HeaderBar,
            FooterBar,
            HomePage
        },
        setup() {
            const router = useRouter();
            onMounted(() => {
            });

            // 监听鼠标滚动
            $(window).scroll(function(){
                var top = $('#header');
                var nav = $('#nav');
                var main = $('#content');
                if(document.documentElement.scrollTop > top.outerHeight()){
                    nav.css({'position':"fixed","top":'0','width':'100%'});
                    nav.children('ul').removeClass('py-2');
                    nav.children('ul').addClass('pt-3');
                    main.css('marginTop',nav.outerHeight()+'px');
                }else{
                    nav.css('position','static');
                    main.css('marginTop','0px');
                    nav.children('ul').removeClass('pt-3');
                    nav.children('ul').addClass('py-2');
                }
            });

            const test = () => {
                console.log('hello');
                router.replace({path: '/category'});
            };
            return {test}
        }
    }
</script>
<style lang="scss">
    @import "assets/css/iconfont_css/iconfont.css";
    @import 'assets/css/base.css';
    @import 'assets/css/other.css';

    #app {
        font-family: Avenir, Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
    }

    .el-image__inner {
        vertical-align: middle;
    }
    .el-carousel .el-icon-arrow-right,.el-carousel .el-icon-arrow-left {
        font-size: 30px;
    }
    #my_app {
        width: 100%;
        height: 100%;
        left:0;
        right:0;
        position: relative;
        #nav{
            font-family: Menlo,Monaco,Andale Mono,lucida console,Courier New,monospace;
            font-weight:bold;
            font-size:18px;
        }
        a {
            color: #2c3e50;
            text-decoration: none;

            &.router-link-exact-active {
                color: #42b983;
            }
        }

    }

</style>
