import {getCart} from 'network/cart';
import {getAddressDetail} from 'network/address';
import {ElMessage} from 'element-plus';
const actions = {
    // 异步更新购物车数据
    updateCart({commit}){
        getCart().then(res=>{
            //console.log('updateCart当前的数量是',res.data);
            commit('addCart',{count:res.data.length || 0})
        }).catch(err=>{
            //ElMessage.error('当前获取购物车数据失败');
            console.log('当前获取购物车数据失败');
        })
    },
    // 异步更新默认地址数据
    defaultAddress({commit},id){
        getAddressDetail(id).then(res=>{
            // 重新载入修改信息
            //console.log('defaultAddress当前的默认地址数据是',res);
            commit('setAddressId',res.id);          // 先设置为0
            commit('setAddressUserName',res.name);
            commit('setAddressUserPhone',res.tel);
            commit('setAddressUserProvince',res.province);
            commit('setAddressUserCity',res.city);
            commit('setAddressUserCounty',res.county);
            commit('setAddressUserDetail',res.addressDetail);
            commit('setHaveDefaultAddress',true);
        }).catch(err=>{
           // ElMessage.error('当前获取默认地址数据失败');
            console.log('当前获取默认地址数据失败');
        })
    }
};
export default actions;