<template>
    <!--商品内容-->
    <div class="goods-item col-6 col-sm-4 col-md-3 col-lg-2 my-1">
        <img :src="product.cover_url" alt="" style="max-width:180px;" @click="itemClick">
        <div class="goods-info d-flex flex-column justify-content-start align-items-center">
            <p class="mb-0">{{product.title}}</p>
            <div class="d-flex justify-content-around w-100 my-1 mx-0 mx-sm-2 mx-md-1 mx-lg-0">
                <span class="price"><small>￥</small>{{product.price}}</span>
                <span class="collect text-muted d-inline">
                    <small>收藏:</small>
                    <span>{{product.collects_count}}</span>
                    <div @click="collectBook(product.id)" class="mt-n2 d-inline">
                        <img src="~assets/images/collection-fill.png" v-if="isCollect" style="max-width:26px;" alt="">
                        <img src="~assets/images/collection.png" v-else style="max-width:26px;" alt="">
                    </div>
                </span>
            </div>
        </div>
    </div>

</template>

<script>
    import $ from 'jquery';
    import {ElLoading, ElMessage} from 'element-plus';
    import {changeCollection} from 'network/collect';

    import {useRouter} from 'vue-router';
    import {ref} from 'vue';

    export default {
        name: "GoodsListItem",
        props: {
            product: {
                type: Object,
                default() {
                    return {}
                }
            }
        },
        setup(props) {
            const router = useRouter();
            const isCollect = ref(false);

            const itemClick = () => {
                console.log('我被点击啦！' + props.product.id);
                router.push({path: '/detail', query: {id: props.product.id}});
                //商品详情页下的相关图书，无法跳转页面
            };
            const collectBook = (id) => {
                changeCollection(id).then(res=>{
                    if(res.status === 201){
                        ElMessage.success('已经收藏商品');
                        isCollect.value = !isCollect.value;
                    }else if(res.status === 204){
                        ElMessage.success('已经取消收藏');
                        isCollect.value = !isCollect.value;
                    } else{
                        ElMessage.warning('注意：收藏商品需要登录');
                    }
                }).catch(err=>{
                    console.log('goodListItem-err',err);
                    ElMessage.error('收藏错误！请重试')
                });
            };
            return {itemClick, collectBook, isCollect}
        }
    }
</script>


