const mutations = {
    // 设置登录状态
    setIsLogin(state,payload){
        state.user.islogin = payload;
    },
    // 添加购物车
    addCart(state,payload){
        state.cartCount = payload.count;
    },
    // 设置购物车中商品的数据  （主动设置）
    setShopCount(state,newCount){
        state.cartCount = newCount;
    },
    // 设置默认地址的Id
    setAddressId(state,newAddressId){
        state.addressId = newAddressId;
        //console.log("setAddressId",state.addressId)
    },
    // 设置默认地址的
    setAddressUserName(state,newAddressUserName){
        state.addressUserName = newAddressUserName;
        //console.log("setAddressUserName",state.addressUserName)
    },
    // 设置默认地址的Phone
    setAddressUserPhone(state,newAddressUserPhone){
        state.addressUserPhone = newAddressUserPhone;
        //console.log('setAddressUserPhone',state.addressUserPhone);
    },
    // 设置默认地址的Province
    setAddressUserProvince(state,newAddressUserProvince){
        state.addressUserProvince = newAddressUserProvince;
        //console.log('setAddressUserProvince',state.addressUserProvince);
    },
    // 设置默认地址的City
    setAddressUserCity(state,newAddressUserCity){
        state.addressUserCity = newAddressUserCity;
        //console.log('setAddressUserCity',state.addressUserCity);
    },
    // 设置默认地址的County
    setAddressUserCounty(state,newAddressUserCounty){
        state.addressUserCounty = newAddressUserCounty;
        //console.log('setAddressUserCounty',state.addressUserCounty);
    },
    // 设置默认地址的Detail
    setAddressUserDetail(state,newAddressUserDetail){
        state.addressUserDetail = newAddressUserDetail;
        //console.log('setAddressUserDetail',state.addressUserDetail);
    },
    // 设置当前帐号的用户头像
    setUserFace(state,newUserFace){
        state.userFace = newUserFace;
        //console.log('setUserFace',state.userFace);
    },
    // 设置当前帐号的用户昵称
    setUserNickname(state,newUserNickname){
        state.userNickname = newUserNickname;
        //console.log('setUserNickname',state.userNickname);
    },
    // 设置当前帐号的用户Email
    setUserEmail(state,newUserEmail){
        state.userEmail = newUserEmail;
        //console.log('setUserEmail',state.userEmail);
    },
};

export default mutations;